var React = require('react');

const eventProps = ['beforeMount', 'onMount', 'onUnmount', 'onError'];

module.exports = function jsx(type, props, ...children) {
  const win = window.__isZMF__ ? window.rawWindow : window;
  const appTagName = 'zmf-app';
  const newProps = { ...props };

  if (type === appTagName && win.customElements.get(appTagName)) {
    newProps.ref = function refCallback(el) {
      // el is web component instance
      if (el) {
        const eventsName = Object.keys(newProps).filter(
          propKey => eventProps.includes(propKey) && typeof newProps[propKey] === 'function'
        );

        eventsName.forEach(eventName => {
          const eventNameId = `${newProps.name}_${newProps.module}:${eventName}`;
          el.addEventListener(eventNameId, newProps[eventName], false);
        });
      }
    };
  }

  return React.createElement.apply(null, [type, newProps, ...children]);
};
